import { configureStore, createSlice } from '@reduxjs/toolkit'

let user = createSlice({
    name : 'user',
    initialState : {name: 'kim', age : 20},
    reducers : 
    {
        changeName(state){
            state.name = 'park' //직접수정도 되네? immer라는 라이브러리가 깔려있어서 그렇다. return 안써도 됨.
        },

        changeAge(state,a){
            state.age +=a.payload;
        },
    }
})// 스테이트 만들기 (이름, 초기값)

export let { changeName, changeAge } = user.actions //오른쪽 자료를 변수로 빼기위해 디스트럭쳐한 문법 {}

let stock = createSlice({
    name : 'stock',
    initialState : [10,11,12]
})// 스테이트 만들기 (이름, 초기값)

let cart = createSlice({
    name : 'cart',
    initialState : [
    {id : 0, name : 'White and Black', count : 2},
    {id : 2, name : 'Grey Yordan', count : 1}

    ],
    reducers : //1)스테이트 수정함수 만들기
    {
        addCount(state, action){
            let 번호 = state.findIndex((a)=> a.id ===action.payload) // id 같은 데이터가 몇번째 항목인지를 알려줌. 0번째이면 0. 
            state[번호].count++;
        },

        addItem(state, action){
            state.push(action.payload) //state라는 어레이에다가 추가
        },
    }

})
export let { addCount, addItem } = cart.actions;
//카트 변수에 담은 스테이트는 함수가 2가지가 있는데, 그 중 addItem을 사용하려면 먼저 export 해주고 가져다 쓰면 된다. 

export default configureStore({
    reducer: {
        //여기다가 등록
        user : user.reducer,
        stock : stock.reducer,
        cart : cart.reducer
    }
})