import logo from "./logo.svg";
import "./App.css";
import { Button, Navbar, Nav, Container, Col, Row } from "react-bootstrap";
import { lazy, Suspense, useState, createContext, useEffect, useTransition, useDeferredValue } from "react";
import data from "./data.js";
import { Routes, Route, Link, useNavigate, Outlet } from "react-router-dom";
import axios from "axios";
// import Detail from "./routes/detail.js"; //메인에서 로드할 필요 없음
// import Cart from "./routes/Cart.js"; //메인에서 로드할 필요 없음
import { useDispatch } from "react-redux";
import { useQuery } from "react-query";
import { isPending } from "@reduxjs/toolkit";

//1. 하기 선언 (컨텍스트는 스테이트 보관함임)
export let Context1 = createContext();

const Detail = lazy(()=>import("./routes/detail.js"));
const Cart = lazy(()=>import("./routes/Cart.js"));

function App() {
  let navigate = useNavigate(); //훅
  let [재고] = useState([10, 11, 12]); //이걸 탭컨텐트에서 쓰고싶으면? contextAPI 사용
  let [shoes, setShoes] = useState(data);
  // 1. shoes는 내용변경없이 data만 앞단에서 추가해주면 되는부분
  let copyShoes = shoes.sort(function (a, b) {
    return a.price - b.price;
  });
  let [name, setName] = useState('')

  useEffect(() => {
    if (false && localStorage.getItem("watched")) {
      localStorage.setItem("watched", JSON.stringify([]));
    }
  }, []);

  


  let result = useQuery("작명", () =>
    axios.get('https://codingapple1.github.io/userdata.json').then((a) => {
      console.log('요청됨')
      return a.data;
    })
  )

  let a = new Array(10000).fill('행복한 수수')
  let [isPending, startTransition] = useTransition()
  let state = useDeferredValue(name)

  return (
    <div className='App'>
      <Navbar bg='light' varixant='light'>
        <Container>
          <Navbar.Brand href='#home'>Nike korea</Navbar.Brand>
          <Nav className='me-auto'>
            <Nav.Link
              href='/home'
              onClick={() => {
                navigate("/");
              }}
            >
              {" "}
              Home
            </Nav.Link>
            <Nav.Link
              href='#all'
              onClick={() => {
                navigate("/all");
              }}
            >
              전체보기
            </Nav.Link>
            <Nav.Link href='/cart'>장바구니</Nav.Link>
            <Nav.Link
              href='#shoes'
              onClick={() => {
                navigate("/clothes");
              }}
            >
              신발
            </Nav.Link>
            <Nav.Link href='#search'>매장검색</Nav.Link>
            <Nav.Link
              href='#event'
              onClick={() => {
                navigate("/event");
              }}
            >
              이벤트
            </Nav.Link>
            <Nav.Link
              href='#service'
              onClick={() => {
                navigate("/service");
              }}
            >
              고객센터
            </Nav.Link>
          </Nav>
          <Nav className='ms-auto'>
            <div>
              <input onChange={(e)=>{
                //트랜지션 콜백함수 안에 입혀주기 
                startTransition(()=>{ setName(e.target.value) })
                }}/>
              { //html 만개를 돌리는 코드. 굳이 이렇게 만개를 다 보여줘야 한다면 트랜지션 사용.
               isPending? '로딩중' : 
               a.map(()=>{
                  return <div>{state}</div>
                })
              }
            </div>
          </Nav>
        </Container>
      </Navbar>
      <div></div>
      {/* 어레이자료형은 이 괄호안에 들어가있으면 돼요 */}
        
      <div></div>
      <div className='main-bg'></div>
      {/* 여기부터 바뀌도록 */}
      <Routes>
        <Route
          path='/'
          element={
            <div className='container'>
              <button
                onClick={() => {
                  //로딩중 UI 띄우기
                  axios
                    .get("https://codingapple1.github.io/shop/data2.json")
                    .then((결과) => {
                      let copy = [...shoes, ...결과.data]; // {}{}{},{}{}{} 요렇게 생긴 모습일거다. 카피는
                      setShoes(copy);
                      console.log(결과.data);
                      //이렇게 데이터를 푸시하면 되는지 테스트
                      //가져온 데이터를 슈즈에 추가해 주세요.
                      console.log(shoes);
                      //위의 큰 어레이 안에 오브젝트가 들어있다.
                      //구글링하면 concat 쓰라고 나온다.
                      //까대기 까는거는 [... ...]임을 명시
                      //숙제 : url data3 요청했을때 7,8,9 보여주기.
                      //버튼누른 횟수를 저장해놓으면 좋을듯. 스테이트로.
                      //로딩중 UI 숨기기
                    })
                    .catch(() => {
                      console.log("실패함 ㅅㄱ");
                      //로딩중 UI 숨기기
                    });

                  // Promise.all([axios.get('/url1'), axios.get('/url2')]) //네이버웹툰서버, 유투브서버에게 각각 데이터를 요청하고 싶으면 이렇게 프로미스 올씀.
                  // fetch('https://어쩌구저쩌구')
                  // .then(결과=>결과.json())//axois와 달리 페치는 브라우저 함수라 자동으로 어레이, 오브젝트로 변환을 안해준다. json 그대로 데이터라 얘네를 변환해줘야함.
                  // .then(data={})//axois와 달리 페치는 브라우저 함수라 자동으로 어레이, 오브젝트로 변환을 안해준다. json 그대로 데이터라 얘네를 변환해줘야함.
                }}
              >
                ajax 요청
              </button>

              <button
                onClick={() => {
                  axios
                    .get("https://codingapple1.github.io/shop/data3.json")
                    .then((결과) => {
                      let copy1 = [...shoes, ...결과.data]; // {}{}{},{}{}{} 요렇게 생긴 모습일거다. 카피는
                      setShoes(copy1);
                    })
                    .catch(() => {
                      console.log("실패함 ㅅㄱ");
                    });
                }}
              >
                ajax 추가요청
              </button>

              <button
                className='btn btn-danger'
                onClick={() => {
                  setShoes(copyShoes);
                  console.log(copyShoes);
                }}
              >
                정렬
              </button>
              <div className='row'>
                {shoes.map((a, i) => {
                  return (
                    <Product shoes={shoes[i]} i={i + 1} navigate={navigate} />
                  );
                })}
              </div>
            </div>
          }
        ></Route>
        <Route 
        path='/detail' element={<Suspense fallback={<div>로딩중임</div>}><Detail shoes={shoes} />
        </Suspense>} />
        <Route
          path='/detail/:id'
          element={
            <Context1.Provider value={{ 재고, shoes }}>
              <Detail shoes={shoes} />
            </Context1.Provider>
          }
        ></Route>
        {/* 슬래시 뒤에 아무거나 적었을때 이 페이지를 보여주세요 */}
        <Route
          path='/about'
          element={
            <div>
              메롱<Outlet></Outlet>
            </div>
          }
        >
          <Route path='location' element={<div>위치임</div>}></Route>
          <Route path='member' element={<div>멤버임</div>}></Route>
        </Route>
        <Route path='/cart' element={<Cart></Cart>}></Route>
        <Route path='*' element={<div>404</div>}></Route>
      </Routes>
    </div>
  );
}

function About() {
  return (
    <div>
      <h4>회사정보임</h4>
    </div>
  );
}

function Product(props) {
  return (
    <div className='col-md-4'>
      <img
        onClick={() => {
          props.navigate("/detail/" + props.i);
        }}
        src={"https://codingapple1.github.io/shop/shoes" + props.i + ".jpg"}
        width='80%'
      />
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.price}</p>
      <p>{props.shoes.content}</p>
    </div>
  );
}

export default App;
